import React from 'react';
import './settings.css';
import {reduxForm, Field} from 'redux-form';
import {Input} from '../../tools/forms.jsx';
import {minLength, maxLength, didTouch} from '../../tools/validation.js';

	const minLength3 = minLength(3);
	const maxLength15 = maxLength(15);
	const maxLength75 = maxLength(75);

const Settings = (props) => {

	const uploadFile = (e) => {
		let file = e.target.files;
		props.putPhotoThunk(file[0]);
	}

	return (
		<div className="settings">
			<ReduxForm onSubmit={(data) => props.updateProfileThunk({...data, lookingForAJobDescription: 'yes'})} {...props} />
			<div className="settings__photo settings__block">
				<div className="settings__photo--text">Change the main photo</div>
				<div className="settings__photo--block">
					<label htmlFor="file">Choose a file...</label>
					<input id="file" name="file" className="settings__setPhoto" onChange={uploadFile} type="file" />
					<label htmlFor="file">Browse</label>
				</div>
			</div>
			<div className="settings__logout settings__block">
				<button onClick={() => props.setLogoutWindow(true)} >Logout</button>
				<div className={"settings__window--block" + ' ' + (props.logoutWindow ? 'active' : '')}>
					<span onClick={() => props.setLogoutWindow(false)} className="settings__window--bg"></span>
					<div className="settings__window">
						<h3>Are you sure?</h3>
						<div className="setting__buttons">
							<button onClick={() => props.setLogoutWindow(false)} className="settings__cancel">Cancel</button>
							<button onClick={() => {props.logoutThunk(); props.setLogoutWindow(false)}}>Logout</button>
						</div>
					</div>
				</div>
			</div>
		</div>
	);
}

const Form = (props) => {
	return (
		<form onSubmit={props.handleSubmit} className="settings__info settings__block">
			<div className="settings__info--item">
				<p>Name</p>
				<Field name="fullName" validate={[didTouch, minLength3, maxLength15]} component={Input} />
			</div>
			<div className="settings__info--item">
				<p>About</p>
				<Field name="aboutMe" validate={[maxLength75]} component={Input} />
			</div>
			<div className="settings__info--btn">
				<button>Send</button>
			</div>
		</form>
	)
}

const ReduxForm = reduxForm({form: 'settings-info'})(Form);

export default Settings;